<template>
  <form @submit.prevent="submitForm">
    <div class="form-group row">
      <label for="name" class="col-sm-3 col-form-label">নাম</label>
      <div class="col-sm-9">
        <input
          type="text"
          class="form-control"
          id="name"
          v-model="name"
          placeholder="ইউনিয়নের নাম লিখুন"
          required
          :disabled="isSaving"
        />
      </div>
    </div>

    <div class="form-group row">
      <div class="col-sm-9 offset-sm-3">
        <button
          type="submit"
          class="btn btn-success btn-block"
          :disabled="isSaving"
        >
          <div
            v-if="isSaving"
            class="spinner-border spinner-border-sm mb-2"
            role="status"
          >
            <span class="sr-only">Saving</span>
          </div>
          Save
        </button>
      </div>
    </div>
  </form>
</template>

<script>
import { firestore, Timestamp } from "../../../config/firebase";

export default {
  name: "UnionFormComponent",
  data: () => ({
    name: null,
    isSaving: false
  }),
  methods: {
    async submitForm() {
      // show preloader
      this.isSaving = true;

      try {
        const docRef = firestore.collection("unions").doc();

        await docRef.set({
          id: docRef.id,
          name: this.name,
          createdAt: Timestamp.now(),
          updatedAt: Timestamp.now()
        });

        // set null in name
        this.name = "";
      } catch (e) {
        alert("Failed to add union. Try again later");
      }

      // hide preloader
      this.isSaving = false;
    }
  }
};
</script>

<style scoped></style>
