<template>
  <tr>
    <th scope="row">{{ index + 1 }}</th>
    <td>
      <form v-if="isEdit">
        <input
          class="form-control"
          type="text"
          v-model="name"
          ref="inputField"
          placeholder="Enter union name"
          required
          :disabled="isUpdating"
        />
        <small
          class="form-text text-danger"
          v-if="errorMessage"
          v-text="errorMessage"
        />
      </form>
      <span v-else>{{ union.name }}</span>
    </td>
    <td>
      <button
        class="btn btn-sm btn-info mr-2"
        v-if="!isEdit"
        @click="isEdit = true"
      >
        Edit
      </button>
      <button
        class="btn btn-sm btn-danger"
        v-if="!isEdit"
        @click="deleteUnion"
        :disabled="isDeleting"
      >
        <div
          v-if="isDeleting"
          class="spinner-border spinner-border-sm mb-2"
          role="status"
        >
          <span class="sr-only">Loading</span>
        </div>
        Delete
      </button>
      <button
        type="button"
        class="btn btn-sm btn-success mr-2"
        v-if="isEdit"
        @click="updateUnion"
        :disabled="isUpdating"
      >
        <div
          v-if="isUpdating"
          class="spinner-border spinner-border-sm mb-2"
          role="status"
        >
          <span class="sr-only">Loading</span>
        </div>
        Update
      </button>
      <button
        class="btn btn-sm btn-info"
        v-if="isEdit"
        @click="isUpdating ? '' : (isEdit = false)"
        :disabled="isUpdating"
      >
        Cancel
      </button>
    </td>
  </tr>
</template>

<script>
import { firestore, Timestamp } from "../../../config/firebase";

export default {
  name: "UnionItemComponent",
  props: {
    union: {
      type: Object,
      required: true
    },
    index: {
      type: Number,
      required: true
    }
  },
  watch: {
    union: {
      handler: function({ name }) {
        this.name = name;
      },
      immediate: true,
      deep: true
    }
  },
  data: () => ({
    name: "",
    isEdit: false,
    errorMessage: null,
    isUpdating: false,
    isDeleting: false
  }),
  methods: {
    async updateUnion() {
      // if already updating or deleting then stop
      if (this.isUpdating || this.isDeleting) {
        return;
      }

      // reset error message
      this.errorMessage = null;
      if (this.name.length === 0) {
        this.errorMessage = "Name should not empty";
        this.$refs.inputField.focus();
        return;
      }

      // show preloader
      this.isUpdating = true;

      try {
        const docRef = firestore.collection("unions").doc(this.union.id);

        await docRef.update({
          name: this.name,
          updatedAt: Timestamp.now()
        });
        // change edit status
        this.isEdit = false;
      } catch (e) {
        alert("Failed to update. Try again later");
      }

      // hide preloader
      this.isUpdating = false;
    },
    async deleteUnion() {
      // if already updating or deleting then stop
      if (this.isUpdating || this.isDeleting) {
        return;
      }

      if (
        !confirm(
          "Are you sure want to delete? This will also delete patients of this union."
        )
      ) {
        return;
      }

      // show preloader
      this.isDeleting = true;

      try {
        // get all patients of this union
        const querySnapshot = await firestore
          .collection("patients")
          .where("unionId", "==", this.union.id)
          .get();

        // delete each patients
        querySnapshot.forEach(async doc => {
          await doc.ref.delete();
        });

        const docRef = firestore.collection("unions").doc(this.union.id);
        await docRef.delete();
      } catch (e) {
        console.log(e);
        console.log("Failed to delete. Try again later");
      }
      // hide preloader
      this.isDeleting = false;
    }
  }
};
</script>

<style scoped></style>
