<template>
  <table class="table">
    <thead>
      <tr>
        <th scope="col">#</th>
        <th scope="col">নাম</th>
        <th scope="col">Action</th>
      </tr>
    </thead>
    <tbody>
      <tr v-if="unions === null">
        <td colspan="3">
          <preloader-component />
        </td>
      </tr>
      <tr v-else-if="unions.length === 0">
        <td colspan="3" class="text-center">
          No union is available
        </td>
      </tr>
      <union-item-component
        v-else
        v-for="(union, index) in unions"
        :key="index"
        :index="index"
        :union="union"
      />
    </tbody>
  </table>
</template>

<script>
import { mapState, mapActions } from "vuex";
import PreloaderComponent from "../../common/PreloaderComponent";
import UnionItemComponent from "./UnionItemComponent";
export default {
  name: "UnionListComponent",
  components: { UnionItemComponent, PreloaderComponent },
  computed: {
    ...mapState(["unions"])
  },
  methods: {
    ...mapActions(["bindUnions"]),
    initUnion() {
      if (this.unions === null) {
        this.bindUnions();
      }
    }
  },
  mounted() {
    this.initUnion();
  }
};
</script>

<style scoped></style>
